body,
html {
  min-width: 1200px;
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
a {
  display: inline-block;
  text-decoration: none;
  color: #2891d1;
}
a:hover {
  color: #2891d1;
}
img {
  max-width: 100%;
}
#app {
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.cursor-p {
  cursor: pointer;
}
.el-table th {
  border-left: 2px solid #98c9e6;
  color: #fff;
  background-color: #2891d1;
}
.app-header .container {
  padding: 0 5%;
}
.app-header .top-bar {
  overflow: hidden;
  border-bottom: 1px solid #eaeaea;
}
.app-header .top-bar >div {
  float: left;
  color: #b6b6b6;
}
.app-header .top-bar >div span {
  color: #000;
}
.app-header .top-bar .social-icons {
  float: right;
  line-height: 53px;
}
.app-header .top-bar .social-icons li {
  float: right;
  cursor: pointer;
  border-right: 1px solid #eaeaea;
  padding: 0 16px;
  -webkit-transition: background-color 0.3s ease;
  transition: background-color 0.3s ease;
}
.app-header .top-bar .social-icons li:last-child {
  border-left: 1px solid #eaeaea;
}
.app-header .top-bar .social-icons li:hover {
  background-color: #6aaf08;
  color: #fff;
}
.app-header .top-bar .social-icons li:hover i {
  color: #fff;
}
.app-header .top-bar .social-icons li i {
  color: #a4a4a4;
  margin-right: 5px;
  font-size: 20px;
}
.contact-info {
  overflow: hidden;
  margin: 30px auto;
}
.contact-info .logo {
  float: left;
}
.contact-info .header-right-info {
  float: right;
}
.contact-info .header-right-info .info-item {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}
.contact-info .header-right-info .info-item .icon-box {
  display: inline-block;
  vertical-align: bottom;
  margin-right: 10px;
  padding: 20px;
  border-radius: 50%;
  border: 1px solid #ddd;
}
.contact-info .header-right-info .info-item .icon-box i {
  font-size: 30px;
  color: #6aaf08;
}
.contact-info .header-right-info .info-item button {
  outline: 0;
  cursor: pointer;
  background: #6aaf08;
  border-radius: 30px;
  display: inline-block;
  color: #fff;
  font-size: 12px;
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  padding: 12px 35px;
  border: 2px solid transparent;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.contact-info .header-right-info .info-item button:hover {
  background-color: #fff;
  color: #6aaf08;
  border-color: #6aaf08;
}
.contact-info .header-right-info .content {
  display: inline-block;
  font-size: 13px;
}
.header-menu {
  background-color: #2891d1;
  border-bottom: 4px solid #6aaf08;
}
.header-menu .navigation {
  overflow: hidden;
}
.header-menu .navigation .nav-tabs {
  float: left;
}
.header-menu .navigation .nav-tabs li {
  float: left;
  line-height: 66px;
}
.header-menu .navigation .nav-tabs li a {
  color: #fff;
  display: block;
  font-size: 14px;
  font-weight: 600;
  -webkit-transition: color 0.3s ease;
  transition: color 0.3s ease;
  margin: 0 20px;
}
.header-menu .navigation .nav-tabs li a:hover {
  color: #6aaf08;
}
.header-menu .navigation .nav-search {
  float: right;
}
.header-menu .navigation .nav-search input,
.header-menu .navigation .nav-search button {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 15px;
  color: #dad9d9;
  line-height: 65px;
}
.header-menu .navigation .nav-search input {
  width: 80%;
}
.header-menu .navigation .nav-search button {
  width: 15%;
}
.header-menu.fadeInDown {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  top: 0;
  z-index: 9;
  width: 100%;
  padding: 0 5%;
}
.header-menu .login-entry {
  float: right;
  line-height: 66px;
  color: #fff;
}
.header-menu .login-entry a {
  color: #fff;
}
.header-menu .login-entry a:hover {
  text-decoration: underline;
}
.header-menu .login-entry a:first-of-type {
  margin-right: 3px;
}
.header-menu .login-entry a:last-of-type {
  margin-left: 3px;
}
.app-footer {
  position: relative;
  padding: 90px 5%;
  background-color: #262626;
  color: #c9c9c9;
}
.app-footer .footer-title {
  position: relative;
  font-size: 20px;
  text-transform: capitalize;
  font-weight: bold;
  color: #fef9f9;
  margin: 0;
  margin-bottom: 35px;
}
.app-footer .footer-title::after {
  background: #6aaf08;
  bottom: -15px;
  content: "";
  display: block;
  height: 2px;
  left: 0;
  margin-top: 0;
  position: absolute;
  width: 40px;
}
.app-footer .info .mg-p {
  margin: 12px 0 21px;
}
.app-footer .info .contact li {
  font-size: 13px;
  margin-bottom: 18px;
  color: #c9c9c9;
}
.app-footer .info .contact i {
  font-size: 20px;
  color: #fff;
  margin-right: 12px;
  vertical-align: middle;
}
.app-footer .info .social li {
  float: left;
  margin-right: 6px;
  padding: 7px;
  border: 1px solid #c9c9c9;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: background-color ease 0.2s;
  transition: background-color ease 0.2s;
}
.app-footer .info .social li a {
  color: #c9c9c9;
